<template>
  <div>
    <el-table
      :data="student"
      style="width: 100%;margin-top:20px"
    >
      <el-table-column
        label=""
        prop="student.stuName"
        sortable
        width="90"
        :show-overflow-tooltip="true"
        align="center"
      />
      <el-table-column
        label=""
        :show-overflow-tooltip="true"
        align="center"
        width="160"
      >
        <template slot-scope="scope">
          {{ scope.row.sex ===1?'':'' }}
        </template>
      </el-table-column>
      <el-table-column
        label=""
        prop="student.personalNum"
        width="190"
        :show-overflow-tooltip="true"
        align="center"
      />
      <el-table-column
        label=""
        :show-overflow-tooltip="true"
        align="center"
      >
        <template slot-scope="scope">
          <span v-if="scope.row.flag ===1">
            
          </span>
          <span v-if="scope.row.flag ===2">
            
          </span>
          <span v-if="scope.row.flag ===3">
            
          </span>
          <span v-if="scope.row.flag ===4">
            
          </span>
          <span v-if="scope.row.flag ===5">
            
          </span>
          <span v-if="scope.row.flag ===6">
            
          </span>
          <span v-if="scope.row.flag ===7">
            
          </span>
          <span v-if="scope.row.flag ===8">
            
          </span>
          <span v-if="scope.row.flag ===9">
            
          </span>
          <span v-if="scope.row.flag ===10">
            
          </span>
          <span v-if="scope.row.flag ===11">
            
          </span>
          <span v-if="scope.row.flag ===12">
            
          </span>
        </template>
      </el-table-column>
      <el-table-column
        label=""
        width="90"
        :show-overflow-tooltip="true"
        align="center"
      >
        <template slot-scope="scope">
          {{ scope.row.flags === null?'':scope.row.flags }}
        </template>
      </el-table-column>
      <el-table-column
        label=""
        align="center"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="danger"
            @click="handleDel(scope.row.id)"
          >
            
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  props: {
    // eslint-disable-next-line vue/require-default-prop
    student: [String, Array],
  },
  methods: {
    handleDel(id) {
      this.$http({
        method: 'POST',
        url: '/pjsys/deleteById.action',
        params: {
          id,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message.success('');
            this.$emit('getStudent');
          } else {
            this.$message.error('');
          }
        });
    },
  },
};
</script>

