<template>
  <div>
    <el-row :gutter="20">
      <el-col :span="3">
        <el-button
          type="primary"
          @click="add"
        >
          
        </el-button>
      </el-col>
      <el-col :span="4">
        <el-button
          type="primary"
          @click="addInter"
        >
          
        </el-button>
      </el-col>
    </el-row>
    <el-dialog
      title=""
      :visible.sync="dialogFormVisible"
      width="500px"
    >
      <el-form
        ref="form"
        label-width="80px"
      >
        <el-form-item label="">
          <el-select
            v-model="value"
            filterable
            placeholder=""
          >
            <el-option
              v-for="item in options"
              :key="item.id"
              :label="item.major"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input
            v-model="subjectName"
            placeholder=""
            style="width:60%"
          />
        </el-form-item>
        <el-form-item label="">
          <el-input
            type="textarea"
            v-model="introduction"
            placeholder=""
            style="width:60%"
          />
        </el-form-item>
        <el-form-item label="">
          <el-radio
            label="1"
            v-model="radio"
          >
            
          </el-radio>
          <el-radio
            label="0"
            v-model="radio"
          >
            
          </el-radio>
        </el-form-item>
      </el-form>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogFormVisible = false">
           
        </el-button>
        <el-button
          type="primary"
          @click="submit"
        >
           
        </el-button>
      </div>
    </el-dialog>
    <el-dialog
      title=""
      :visible.sync="dialogInter"
      width="600px"
    >
      <el-form
        ref="form"
        label-width="80px"
      >
        <el-form-item label="">
          <el-select
            v-model="InterSubject"
            filterable
            placeholder=""
          >
            <el-option
              v-for="item in Interoptions"
              :key="item.id"
              :label="item.interviewSubject"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
            v-model="InterDate"
            type="datetimerange"
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            value-format="yyyy-MM-DD+HH:MM"
          />
        </el-form-item>
        <el-form-item label="">
          <el-input
            v-model="interNum"
            placeholder=""
            style="width:400px"
          />
        </el-form-item>
      </el-form>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogInter = false">
           
        </el-button>
        <el-button
          type="primary"
          @click="interSubmit"
        >
           
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialogFormVisible: false,
      dialogInter: false,
      subjectName: '',
      value: '',
      options: [],
      radio: '1',
      introduction: '',
      InterSubject: '',
      Interoptions: [],
      InterDate: '',
      interNum: '',
    };
  },
  mounted() {
    this.getDiscip();
  },
  methods: {
    getDiscip() {
      this.$http({
        method: 'GET',
        url: '/pjsys/findAllMajors.action',
      })
        .then((res) => {
          this.options = res.data;
        });
    },
    add() {
      this.dialogFormVisible = true;
    },
    submit() {
      this.$http({
        method: 'POST',
        url: '/pjsys/addSubject.action',
        params: {
          major_id: this.value,
          subject: this.subjectName,
          introduction: this.introduction,
          interview: this.radio,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$emit('getSubject');
            this.$message.success('');
            this.dialogFormVisible = false;
          } else {
            this.$message.error('');
          }
        });
    },
    getInterOptions() {
      this.$http({
        method: 'GET',
        url: '/pjsys/findAllInterview.action',
      })
        .then((res) => {
          this.Interoptions = res.data;
        });
    },
    //
    addInter() {
      // 
      this.dialogInter = true;
      this.getInterOptions();
    },
    interSubmit() {
      if (this.InterSubject === ''
      || this.interNum === ''
      || this.InterDate === ''
      ) {
        this.$message.error('');
      } else {
        this.$http({
          method: 'POST',
          url: '/pjsys/addInterview_timeslot.action',
          params: {
            interview_id: this.InterSubject,
            starttime: this.InterDate[0],
            end_time: this.InterDate[1],
            num: this.interNum,
          },
        })
          .then((res) => {
            if (res.data === 1) {
              this.dialogInter = false;
              this.$message.success('');
              this.InterSubject = '';
              this.interNum = '';
              this.InterDate = '';
            } else {
              this.$message.error('');
            }
          });
      }
    },
  },
};
</script>

