<template>
  <div>
    <el-table
      :data="subject"
      style="width: 100%;margin-top:20px"
    >
      <el-table-column
        label=""
        prop="subject"
        width="90"
        :show-overflow-tooltip="true"
        align="center"
      />
      <el-table-column
        label=""
        prop="introduction"
        :show-overflow-tooltip="true"
        align="center"
      />
      <el-table-column
        label=""
        :show-overflow-tooltip="true"
        align="center"
        width="80"
      >
        <template slot-scope="scope">
          {{ scope.row.interview === 0? '':'' }}
        </template>
      </el-table-column>
      <el-table-column
        label=""
        align="center"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="handleEdit(scope.row)"
          >
            
          </el-button>
          <el-button
            v-if="scope.row.interview === 1"
            size="mini"
            type="primary"
            @click="handleDetail(scope.row.interview_id)"
          >
            
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDel(scope.row)"
          >
            
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title=""
      :visible.sync="dialogDeatil"
    >
      <el-table
        :data="detail"
        style="width: 100%;margin-top:20px"
      >
        <el-table-column
          label=""
          prop="starttime"
          :show-overflow-tooltip="true"
          align="center"
        />
        <el-table-column
          prop="end_time"
          label=""
          :show-overflow-tooltip="true"
          align="center"
        />
        <el-table-column
          prop="num"
          label=""
          :show-overflow-tooltip="true"
          align="center"
        />
        <el-table-column
          label=""
          align="center"
        >
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="handleDetailEdit(scope.row)"
            >
              
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogDeatil = false">
           
        </el-button>
      </div>
    </el-dialog>
    <el-dialog
      title=""
      :visible.sync="dialogEditDeatil"
    >
      <el-form
        ref="form"
        label-width="80px"
      >
        <el-form-item label="">
          <el-input
            v-model="InterSubject"
            style="width:400px"
            disabled
            placeholder=""
          />
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
            v-model="InterDate"
            type="datetimerange"
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            value-format="yyyy-MM-DD+HH:MM"
          />
        </el-form-item>
        <el-form-item label="">
          <el-input
            v-model="interNum"
            placeholder=""
            style="width:400px"
          />
        </el-form-item>
      </el-form>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button
          type="primary"
          @click="EditDeatil"
        >
          
        </el-button>
        <el-button @click="dialogEditDeatil = false">
           
        </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogUpdata"
      width="600px"
    >
      <el-form
        ref="form"
        label-width="80px"
      >
        <el-form-item label="">
          <el-select
            v-model="UpdataSubject"
            filterable
            placeholder=""
          >
            <el-option
              v-for="item in Updataoptions"
              :key="item.id"
              :label="item.major"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input
            style="width:400px"
            v-model="UpdataSubjectName"
            placeholder=""
          />
        </el-form-item>
        <el-form-item label="">
          <el-input
            type="textarea"
            v-model="UpdataIntroduction"
            placeholder=""
            style="width:400px"
          />
        </el-form-item>
        <el-form-item label="">
          <el-radio
            label="1"
            v-model="Updataradio"
          >
            
          </el-radio>
          <el-radio
            label="0"
            v-model="Updataradio"
          >
            
          </el-radio>
        </el-form-item>
      </el-form>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogUpdata = false">
           
        </el-button>
        <el-button
          type="primary"
          @click="UpdataSubmit"
        >
           
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: {
    subject: [String, Array],
  },
  data() {
    return {
      detail: [],
      detailId: '',
      dialogDeatil: false,
      dialogEditDeatil: false,
      dialogUpdata: false,
      interNum: '',
      InterDate: '',
      InterSubject: '',
      InterId: '',
      UpdataSubject: '',
      Updataoptions: [],
      UpdataSubjectName: '',
      UpdataIntroduction: '',
      Updataradio: '1',
      UpdaId: '',
    };
  },
  mounted() {
  },
  methods: {
    // 
    handleEdit(row) {
      this.dialogUpdata = true;
      this.getUpdataoptions();
      this.UpdataSubject = row.majors.id;
      this.UpdataSubjectName = row.subject;
      this.UpdaId = row.id;
      this.Updataradio = String(row.interview);
      this.UpdataIntroduction = row.introduction;
    },
    // 
    getUpdataoptions() {
      this.$http({
        method: 'GET',
        url: '/pjsys/findAllMajors.action',
      })
        .then((res) => {
          this.Updataoptions = res.data;
        });
    },
    // 
    UpdataSubmit() {
      if (
        this.UpdataSubject === ''
        || this.UpdataSubjectName === ''
        || this.UpdataIntroduction === ''
        || this.Updataradio === ''
      ) {
        this.$message.error('');
      } else {
        this.$http({
          method: 'POST',
          url: '/pjsys/updateSubject.action',
          params: {
            id: this.UpdaId,
            major_id: this.UpdataSubject,
            subject: this.UpdataSubjectName,
            introduction: this.UpdataIntroduction,
            interview: this.Updataradio,
          },
        })
          .then((res) => {
            if (res.data === 1) {
              this.dialogUpdata = false;
              this.$message.success('');
              this.$emit('getSubject');
            } else {
              this.$message.error('');
            }
          });
      }
    },
    // 
    handleDel(row) {
      this.$http({
        method: 'POST',
        url: '/pjsys/pisys/deleteSubject.action',
        params: {
          id: row.id,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message.success('');
            this.$emit('getSubject');
          } else {
            this.$message.error('');
          }
        });
    },
    // 
    handleDetail(id) {
      this.detailId = id;
      this.dialogDeatil = true;
      this.$http({
        method: 'GET',
        url: '/pjsys/queryInterviewtimeslotActionByinterviewid',
        params: {
          interview_id: id,
        },
      })
        .then((res) => {
          this.detail = res.data;
        });
    },
    // 
    handleDetailEdit(row) {
      this.dialogEditDeatil = true;
      this.interNum = row.num;
      this.InterId = row.id;
      this.InterDate = `${row.starttime}e-${row.end_time}`;
      this.InterSubject = row.interview.interviewSubject;
    },
    // 
    EditDeatil() {
      if (this.InterDate === ''
      || this.InterId === ''
      || this.InterSubject === ''
      || this.interNum === ''
      ) {
        this.$message.error('');
      } else {
        this.$http({
          method: 'POST',
          url: '/pjsys/updateInterviewSubject.action',
          params: {
            interview_timeslot_id: this.InterId,
            num: this.interNum,
            starttime: this.InterDate[0],
            end_time: this.InterDate[1],
          },
        })
          .then((res) => {
            if (res.data === 1) {
              this.$message.success('');
              this.handleDetail(this.detailId);
              this.InterDate = '';
              this.InterId = '';
              this.InterSubject = '';
              this.interNum = '';
              this.dialogEditDeatil = false;
            } else {
              this.$message.error('');
            }
          });
      }
    },
  },
};
</script>

