<template>
  <el-form
    ref="form"
    :model="form"
    label-width="120px"
    style="width:500px"
  >
    <el-form-item label="">
      <el-input
        v-model="form.name"
        placeholder=""
      />
    </el-form-item>
    <el-form-item
      label=""
    >
      <el-input
        v-model="form.card"
        placeholder=""
      />
    </el-form-item>
    <el-form-item label="">
      <el-input
        v-model="form.tel"
        placeholder=""
      />
    </el-form-item>
    <el-form-item label="">
      <el-input
        type="textarea"
        placeholder=""
        v-model="form.desc"
      />
    </el-form-item>
    <el-form-item label="">
      <el-radio
        v-model="form.sex"
        label=""
      />
      <el-radio
        v-model="form.sex"
        label=""
      />
    </el-form-item>
    <el-form-item>
      <el-button
        type="primary"
        @click="onSubmit"
      >
        
      </el-button>
      <el-button @click="back">
        
      </el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  data() {
    return {
      form: {
        name: '',
        desc: '',
        sex: '',
        tel: '',
        card: '',
      },
    };
  },
  methods: {
    back() {
      this.$router.push('/admin/tteacher');
    },
    onSubmit() {
      if (
        this.form.name === ''
        || this.form.tel === ''
        || this.form.card === '') {
        this.$message.error('');
      } else {
        this.add();
      }
    },
    add() {
      this.$http({
        method: 'POST',
        url: '/pjsys/addHeadMaster',
        params: {
          name: this.form.name,
          sex: this.form.sex,
          phonenumber: this.form.tel,
          card: this.form.card,
          comment: this.form.desc,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message.success('');
            this.$router.push('/admin/tteacher');
          } else {
            this.$message.success('');
          }
        });
    },
  },
};
</script>

