<template>
  <div>
    <el-row>
      <el-col :span="5">
        <el-input
          v-model="name"
          @keyup.enter.native="addRoom"
          placeholder=""
        />
      </el-col>
      <el-col :span="3">
        <el-button
          type="primary"
          @click="addRoom"
        >
          
        </el-button>
      </el-col>
      <el-col :span="3">
        <el-button
          type="primary"
          @click="addTime"
        >
          
        </el-button>
      </el-col>
      <el-col :span="3">
        <el-button
          type="primary"
          @click="percent"
        >
          
        </el-button>
      </el-col>
    </el-row>
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="20%"
    >
      <el-form>
        <el-form-item label="">
          <el-select
            v-model="selclass"
            placeholder=""
            @change="getPercent"
          >
            <el-option
              v-for="item in classOptions"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="">
          {{ selPercent }}
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button
          type="primary"
          @click="dialogVisible = false"
        >
           
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      name: '',
      dialogVisible: false,
      selclass: '',
      classOptions: [],
      selPercent: '',
    };
  },
  mounted() {
    this.getRoom();
  },
  methods: {
    addRoom() {
      //
      if (this.name === '') {
        this.$message.error('');
      } else {
        this.$http({
          method: 'POST',
          url: '/pjsys/addClassroom.action',
          params: {
            name: this.name,
          },
        })
          .then((res) => {
            if (res.data === 1) {
              this.$message.success('');
              this.$emit('getRoom');
            } else {
              this.$message.error('');
            }
          });
      }
    },
    percent() {
      //
      this.dialogVisible = true;
    },
    addTime() {
      //
      this.$router.push('/admin/troomtime');
    },
    getRoom() {
      this.$http({
        method: 'GET',
        url: '/pjsys/findAllClassroom.action',
      })
        .then((res) => {
          this.classOptions = res.data;
        });
    },
    // 
    getPercent() {
      this.$http({
        method: 'GET',
        url: '/pjsys/percent.action',
        params: {
          classroom_id: this.selclass,
        },
      })
        .then((res) => {
          this.selPercent = res.data;
        });
    },
  },
};
</script>

