<template>
  <div>
    <el-row
      style="margin-bottom:20px"
    >
      <el-col :span="4">
        <el-input
          v-model="addName"
          @keyup.enter.native="add"
          placeholder=""
        />
      </el-col>
      <el-col :span="4">
        <el-button
          @click="add"
          type="primary"
        >
          
        </el-button>
      </el-col>
    </el-row>
    <el-table
      :data="disciline"
      style="width: 100%"
    >
      <el-table-column
        prop="major"
        label=""
        :show-overflow-tooltip="true"
        align="center"
      />
      <el-table-column
        label=""
        :show-overflow-tooltip="true"
        align="center"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="handleEdit(scope.row)"
          >
            
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDel(scope.row)"
          >
            
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title=""
      :visible.sync="dialogFormVisible"
    >
      <el-input
        v-model="updataName"
        autocomplete="off"
      />
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogFormVisible = false">
           
        </el-button>
        <el-button
          type="primary"
          @click="updata"
        >
           
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      addName: '',
      disciline: [],
      dialogFormVisible: false,
      updataName: '',
      updataId: '',
    };
  },
  mounted() {
    this.get();
  },
  methods: {
    get() {
      this.$http({
        method: 'GET',
        url: '/pjsys/findAllMajors.action',
      })
        .then((res) => {
          this.disciline = res.data;
        });
    },
    handleEdit(row) {
      this.updataName = row.major;
      this.updataId = row.id;
      this.dialogFormVisible = true;
    },
    handleDel(row) {
      this.$http({
        method: 'POST',
        url: '/pjsys/deleteMajors.action',
        params: {
          id: row.id,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message.success('');
            this.get();
          } else {
            this.$message.error('');
          }
        })
        .catch(() => {
          this.$message.error('');
        });
    },
    // 
    add() {
      if (this.addName !== '') {
        this.$http({
          method: 'POST',
          url: '/pjsys/addMajors.action',
          params: {
            major: this.addName,
          },
        })
          .then((res) => {
            if (res.data === 1) {
              this.$message.success('');
              this.addName = '';
              this.get();
            } else {
              this.$message.error('');
            }
          });
      } else {
        this.$message.error('');
      }
    },
    // 
    updata() {
      this.$http({
        method: 'POST',
        url: '/pjsys/updateMajors.action',
        params: {
          id: this.updataId,
          major: this.updataName,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.get();
            this.dialogFormVisible = false;
            this.$message.success('');
          }
        })
        .catch(() => {
          this.$message.error('');
        });
    },
  },

};
</script>

