<template>
  <el-form
    :model="form"
    label-width="80px"
  >
    <el-form-item label="">
      <el-select
        v-model="form.classRoom"
        placeholder=""
      >
        <el-option
          v-for="item in form.classRoomOptions"
          :key="item.id"
          :label="item.name"
          :value="item.id"
        />
      </el-select>
    </el-form-item>
    <el-form-item label="">
      <el-select
        v-model="form.date"
        placeholder=""
      >
        <el-option
          v-for="item in form.dateOptions"
          :key="item.key"
          :label="item.value"
          :value="item.key"
        />
      </el-select>
    </el-form-item>
    <el-form-item label="">
      <el-time-picker
        is-range
        v-model="form.time"
        range-separator=""
        start-placeholder=""
        end-placeholder=""
        placeholder=""
        format="HH:mm"
        value-format="HH:mm"
      />
    </el-form-item>
    <el-form-item>
      <el-button
        @click="back"
        style="width:200px"
      >
        
      </el-button>
      <el-button
        @click="submit"
        type="primary"
        style="width:200px"
      >
        
      </el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  data() {
    return {
      form: {
        classRoom: '',
        classRoomOptions: [],
        time: '',
        date: '',
        dateOptions: [
          {
            key: 1,
            value: '',
          },
          {
            key: 2,
            value: '',
          },
          {
            key: 3,
            value: '',
          },
          {
            key: 4,
            value: '',
          },
          {
            key: 5,
            value: '',
          },
          {
            key: 6,
            value: '',
          },
          {
            key: 7,
            value: '',
          },
        ],
      },
    };
  },
  mounted() {
    this.getRoom();
  },
  methods: {
    getRoom() {
      this.$http({
        method: 'GET',
        url: '/pjsys/findAllClassroom.action',
      })
        .then((res) => {
          this.form.classRoomOptions = res.data;
        });
    },
    back() {
      this.$router.push('/admin/tclassroom');
    },
    submit() {
      if (
        this.form.classRoom === ''
        || this.form.time === ''
        || this.form.date === ''
      ) {
        this.$message.error('');
      } else {
        this.$http({
          method: 'POST',
          url: '/pjsys/addClassroominfo.action',
          params: {
            classroom_id: this.form.classRoom,
            day: this.form.date,
            start: this.form.time[0],
            jieshu: this.form.time[1],
          },
        })
          .then((res) => {
            if (res.data === 1) {
              this.$message.success('');
            } else {
              this.$message.error('');
            }
          });
      }
    },
  },
};
</script>

