<template>
  <el-form label-width="80px">
    <el-form-item label="">
      <el-input
        v-model="header"
        style="width:300px"
        placeholder=","
      />
    </el-form-item>
    <el-form-item label="">
      <el-input
        v-model="content"
        type="textarea"
        style="width:300px"
        placeholder=""
      />
    </el-form-item>
    <el-form-item>
      <el-button
        type="primary"
        @click="send"
        style="width:300px"
      >
        
      </el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  data() {
    return {
      content: '',
      header: '',
      teacherID: '',
    };
  },
  mounted() {
    this.teacherID = this.$Storage.get('teacherID');
    if (this.teacherID === '' || this.teacherID === undefined) {
      this.$message.error(',');
      this.$router.push('/adminlogin');
    }
  },
  methods: {
    send() {
      if (this.content === '' || this.header === '') {
        this.$message.error('');
      } else {
        this.sendNotice();
      }
    },
    sendNotice() {
      const data = this.$qs.stringify({
        teacher_id: this.teacherID,
        'imessageByInter.head': this.header,
        'imessageByInter.message': this.content,
      });
      this.$http({
        method: 'POST',
        url: '/pjsys/addImessageByInter.action',
        data,
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message.success('');
          } else {
            this.$message.error('');
          }
        });
    },
  },

};
</script>

