<template>
  <el-table
    :data="info"
    style="width: 100%;margin-top:20px"
  >
    <el-table-column
      fixed
      label=""
      prop="classinfo.subject.subject"
      width="90"
      show-overflow-tooltip
      align="center"
    />
    <el-table-column
      label=""
      prop="classinfo.classname"
      show-overflow-tooltip
      align="center"
    />
    <el-table-column
      label=""
      show-overflow-tooltip
      align="center"
    >
      <template slot-scope="scope">
        <span v-if="scope.row.classinfo.subject.interview ===1">
          
        </span>
        <span v-if="scope.row.classinfo.subject.interview ===0">
          
        </span>
      </template>
    </el-table-column>
    <el-table-column
      label=""
      show-overflow-tooltip
      align="center"
    >
      <template slot-scope="scope">
        <span v-if="scope.row.flag ===1">
          
        </span>
        <span v-if="scope.row.flag ===2">
          
        </span>
        <span v-if="scope.row.flag ===3">
          
        </span>
        <span v-if="scope.row.flag ===4">
          
        </span>
        <span v-if="scope.row.flag ===5">
          
        </span>
        <span v-if="scope.row.flag ===6">
          
        </span>
        <span v-if="scope.row.flag ===7">
          
        </span>
        <span v-if="scope.row.flag ===8">
          
        </span>
        <span v-if="scope.row.flag ===9">
          
        </span>
        <span v-if="scope.row.flag ===10">
          
        </span>
        <span v-if="scope.row.flag ===11">
          
        </span>
        <span v-if="scope.row.flag ===12">
          
        </span>
      </template>
    </el-table-column>
    <el-table-column
      label=""
      align="center"
    >
      <template slot-scope="scope">
        <el-button
          size="mini"
          type="danger"
          @click="handleDel(scope.row.id)"
        >
          
        </el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
export default {
  props: {
    // eslint-disable-next-line vue/require-default-prop
    info: [Array],
  },
  methods: {
    handleDel(id) {
      this.$http({
        method: 'POST',
        url: '/deleteById.action',
        params: {
          id,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message.success('');
            this.$emit('getInfo');
          } else {
            this.$message.error('');
          }
        });
    },
  },
};
</script>

